.social {
    @extend %flex-align-center;

    &__item {
        &:not(:last-child) {
            margin-right: 12px;
        }
    }

    &__link {
        display: block;
        width: 40px;
        height: 40px;
    }

    &__icon {
        width: 100%;
        height: 100%;
    }
}