@font-face {
  font-family: "Roboto";
  src: local("Roboto"), url("../fonts/Roboto/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto/Roboto-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Roboto";
  src: local("Roboto"), url("../fonts/Roboto/Roboto-Bold.woff2") format("woff2"), url("../fonts/Roboto/Roboto-Bold.woff") format("woff");
  font-weight: 500;
  font-style: normal;
}
.footer__inner, .footer__nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.social, .footer__nav-list, .footer__contacts, .site-nav__list, .user-actions__link, .user-actions, .header__actions, .header__location-item, .header__location {
  display: flex;
  align-items: center;
}

body {
  margin: 0;
  background-color: #fff;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  line-height: 1.2;
  font-weight: 400;
  color: #333;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

p {
  margin: 0 0 10px;
}

a {
  text-decoration: none;
  color: inherit;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

input, textarea, select, button {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  line-height: inherit;
}

.img {
  display: block;
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.text-uppercase {
  text-transform: uppercase;
}

/* Focus-element
================*/
:focus {
  outline: 2px solid #E11C4B;
}

:focus:not(:focus-visible) {
  outline: 0;
}

:focus-visible {
  outline: 2px solid #E11C4B;
}

/* Container
================*/
.container {
  width: 100%;
  max-width: 1180px;
  padding: 0 15px;
  margin: 0 auto;
}

/* Components */
.page {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow: hidden;
}

.header__location-wrapper {
  border-bottom: 2px solid #E8E8E8;
}
.header__location {
  height: 35px;
}
.header__location-icon {
  width: 18px;
  height: 18px;
  margin-right: 6px;
  flex-shrink: 0;
}
.header__city {
  margin-right: 37px;
  padding: 0;
  background: none;
  border: none;
  cursor: pointer;
}
.header__phone {
  display: inline-block;
  margin-left: auto;
  padding-left: 24px;
  padding-top: 4px;
}
.header__actions {
  padding: 8px 0;
}
.header__logo {
  margin-right: 12px;
}

.contacts__link {
  display: inline-block;
  position: relative;
}
.contacts__link--phone:before, .contacts__link--mail:before {
  content: "";
  display: block;
  background-repeat: no-repeat;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}
.contacts__link--phone:before {
  width: 18px;
  height: 18px;
  background-image: url(../images/phone.svg);
}
.contacts__link--mail:before {
  width: 25px;
  height: 17px;
  background-image: url(../images/envelope.svg);
}

.logo {
  width: 227px;
  height: 70px;
}

.search-form {
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 16px;
  border: 2px solid #DDDDDD;
  border-radius: 100px;
}
.search-form__btn {
  width: 26px;
  height: 26px;
  padding: 0;
  margin-right: 5px;
  background: none;
  border: none;
  cursor: pointer;
}
.search-form__btn-icon {
  width: 100%;
  height: 100%;
}
.search-form__input {
  width: 455px;
  border: none;
  outline: none;
}
.search-form__input::placeholder {
  color: #8D8D8D;
}

.user-actions {
  margin-left: auto;
  padding-top: 5px;
}
.user-actions__item:not(:last-child) {
  margin-right: 25px;
}
.user-actions__link {
  flex-direction: column;
}
.user-actions__icon {
  width: 18px;
  height: 18px;
  margin-bottom: 2px;
}

.site-nav {
  position: relative;
}
.site-nav__item {
  margin-right: 36px;
}
.site-nav__item.swiper-slide {
  width: auto !important;
}
.site-nav__link--active {
  color: #E11C4B;
}
.site-nav__link--promotion {
  padding-left: 24px;
  position: relative;
}
.site-nav__link--promotion:before {
  content: "";
  display: block;
  width: 18px;
  height: 18px;
  background-image: url(../images/promotion.svg);
  background-repeat: no-repeat;
  position: absolute;
  top: 0;
  left: 0;
}
.site-nav__btn.swiper-button-prev, .site-nav__btn.swiper-button-next {
  display: block;
  width: 18px;
  height: 18px;
  margin: 0;
  padding: 0;
  background: #fff;
  border: none;
  cursor: pointer;
  position: absolute;
  top: 50%;
  z-index: 2;
  transform: translateY(-50%);
}
.site-nav__btn--prev.swiper-button-prev {
  left: 0;
}
.site-nav__btn--next.swiper-button-next {
  right: 0;
}
.site-nav__btn.swiper-button-next.swiper-button-disabled, .site-nav__btn.swiper-button-prev.swiper-button-disabled {
  opacity: 0;
}
.site-nav__btn-icon {
  width: 100%;
  height: 100%;
}

.promotions {
  padding-top: 36px;
  padding-bottom: 93px;
}
.promotions__title {
  margin-bottom: 36px;
  font-size: 36px;
  font-weight: 700;
  line-height: 1.15;
}
.promotions__list {
  display: flex;
  flex-wrap: wrap;
  column-gap: 33px;
  row-gap: 34px;
}
.promotions__item {
  flex-basis: calc((100% - 66px) / 3);
  border-radius: 10px;
  box-shadow: 0px 6px 30px 4px rgba(32, 32, 32, 0.1);
}
.promotions__item-img {
  width: 100%;
  object-fit: cover;
}

.footer {
  margin-bottom: 37px;
  padding: 36px 0 18px;
  background-color: #F4F4F4;
}
.footer__nav {
  margin-bottom: 42px;
}
.footer__nav-item:not(:last-child) {
  margin-right: 62px;
}
.footer__contacts {
  padding-top: 1px;
}
.footer__contacts-item:not(:last-child) {
  margin-right: 62px;
}
.footer__contacts-phone {
  padding-left: 28px;
}
.footer__contacts-mail {
  padding-left: 34px;
}
.footer__copyright {
  margin-bottom: 0;
  font-size: 14px;
  line-height: 1.4;
  color: #8D8D8D;
}
.footer__copyright span {
  text-transform: uppercase;
}
.footer__social {
  margin-top: -4px;
}

.social__item:not(:last-child) {
  margin-right: 12px;
}
.social__link {
  display: block;
  width: 40px;
  height: 40px;
}
.social__icon {
  width: 100%;
  height: 100%;
}